
<script>
import axios from 'axios';



const imgurl = [{
    game: "ys",
    url: "https://sdk-static.mihoyo.com/hk4e_cn/mdk/launcher/api/content?filter_adv=false&key=eYd89JmJ&language=zh-cn&launcher_id=18",
}, {
    game: "bh3",
    url: "https://sdk-os-static.mihoyo.com/bh3_global/mdk/launcher/api/content?filter_adv=false&key=ojevZ0EyIyZNCy4n&language=ja-jp&launcher_id=19",
},
{
    game: "sr",
    url: "https://api-launcher-static.mihoyo.com/hkrpg_cn/mdk/launcher/api/content?filter_adv=false&key=6KcVuOkbcqjJomjZ&language=zh-cn&launcher_id=33"
}
]

const netdata = [];

const getnetimg = function () {
    var _this = this;
    for (let index = 0; index < imgurl.length; index++) {
        axios
            .get(imgurl[index].url)
            .then((response) => {
                _this.netdata.push(response.data.data);
            });
    }
}




export default {
    name: 'GameInfoDynamicGetback',
    getnetimg,
    imgurl,
    netdata,
    data() {
        return {

        };
    },
};
</script>