<template>
    <el-menu class="logo-menu" background-color="#00000000" mode="horizontal" default-active="this.$router.path" router
        :ellipsis="false" style="border: none;">
        <el-menu-item v-for="(item, index ) in logodata" :key="item.game" :name="item.game" :label="item.game"
            :index="item.path" style="padding: 0px;  width: 60px; height: 60px;margin: 8px;border: none;"
            @click="selectgame(item, index)">
            <el-image :src="item.url" fit="cover" class="image"></el-image>
        </el-menu-item> </el-menu>
</template>
<script>
import DefaultConfig from '@/views/DefaultConfig.vue';

export default {
    name: 'GameInfoDynamicLogotabs',
    DefaultConfig,
    data() {
        return {
            logodata: DefaultConfig.logodata
        };
    },

    mounted() {
    },

    methods: {
        selectgame(e, index) {
            e.id = index
            this.$emit("selectgame", e);
        }
    },
};
</script>
<style >
.image {
    width: 60px;
    height: 60px;
    border-radius: 10px;
}

.el-menu-item.is-active .image {
    background: none !important;
    border: 4px solid white !important;
    transform: scale(1.2);
}


.logo-menu .image:hover {
    border: 4px solid white;
    transform: scale(1.3);
}


.logo-menu .image {
    transition: all 0.3s;
}
</style>