<template>
  <el-container>
    <el-main id="main" :key="0">


      <router-view v-slot="{ Component }">
        <transition :name="changername_mian">
          <component :is="Component" :key="$route.fullPath" />
        </transition>
      </router-view>
    </el-main>
    <transition-group :name="changername">
      <div class="bg" v-show="item.game == gamename" v-for="(item, index ) in logodata"
        :style="{ backgroundImage: 'url(' + item.bg + ')' }" :key="index">
