      </el-row>
      <transition name="el-fade-in-linear">
        <el-row id="startpage" justify="end" type="flex" v-show="startview" v-if="false">
          <el-button-group>
            <el-button type="primary" color="#40E0D0" plain size="large" @click="startgame">启动</el-button>
            <el-button type="primary" color="#40E0D0" plain size="large" style="width: 20px;font-size: 10px;">
              <el-icon>
