<script>
import DefaultConfig from '@/views/DefaultConfig.vue';
import logotabs from '@/components/logotabs.vue';
import Getback from '@/views/Getback.vue';
import { ArrowUpBold } from '@element-plus/icons-vue'
import { openUrlWithInputTimeoutHack } from '@/components/run';





export default {
  components: { logotabs },
  name: 'GameInfoDynamicHomeView',
  Getback,
  ArrowUpBold,
  data() {
    return {
      startview: false,
      logodata: DefaultConfig.logodata,
      show: true,
      tabsname: "Home",
      bg: new URL('../assets/bg/bg.jpg', import.meta.url).href,
      gamename: "崩坏2",
      data: [],
      gameid: -1,
      changername: "el-fade",
      changername_mian: "el-fade"
    };
  },

  mounted() {
    Getback.getnetimg();

  },

  methods: {
    // eslint-disable-next-line 
    chuangpage(tab, event) {
      var data = document.getElementById("logo");
      if (tab.index == 0) {
        data.style.marginTop = '15px';
        this.startview = false;
        this.$router.push("/")
      } else {
        data.style.marginTop = '-30px';
        this.startview = true;
      }
    },
    selectgame(e) {
      var tab = {
        index: 1
      };
      this.chuangpage(tab, e);
      // if (e.game == "原神") {
      //   e.bg = Getback.netdata[0].adv.background;
      // }
      // if (e.game == "崩坏3") {
      //   e.bg = Getback.netdata[1].adv.background;
      // }
      // if (e.game == "崩坏:星穹铁道") {
      //   e.bg = Getback.netdata[2].adv.background;
      // }
      if (this.gameid > e.id) {
        this.changername = "fade2"
      } else {
        this.changername = "fade"
      }
      this.tabsname = "Game";
      this.gameid = e.id;
      this.gamename = e.game;
    },
    startgame() {
      openUrlWithInputTimeoutHack('ys:// ', () => {
        console.log('未安装');
      }, () => {
        console.log('已安装，自动唤起');
      })
    }

  },
};
</script>

